@import "partials/reset.css";
@import "partials/fonts.css";
/* my style */
input,
button,
textarea {
  font-size: inherit;
  font-family: inherit;
  margin: 0;
  padding: 0;
  border: 0;
}

div,
input,
textarea,
select,
button,
h1,
h2,
h3,
h4,
h5,
h6,
a,
span {
  outline: none;
  font-size: inherit;
  background: transparent;
}

* {
  box-sizing: border-box;
}

a,
a:hover {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
}

textarea {
  resize: none;
}

a {
  color: inherit;
}

body {
  display: flex;
  flex-direction: column;
  margin: 0;
  color: #ffffff;
  background-color: #1a1919;
  font-family: "Ubuntu", sans-serif;
  font-size: 16px;
}
body.scroll-stoped {
  overflow: hidden;
}

.main {
  flex-grow: 1;
  background-color: #f9f9f9;
}

html,
body {
  height: 100%;
}

.container {
  box-sizing: border-box;
  width: 1137px;
  max-width: 95%;
  max-width: calc(100% - 60px);
  margin-left: 6.73611vw;
  margin-right: 14.305556vw;
}

@media screen and (min-width: 1500px) {
  .container {
    margin: 0 auto;
  }
}
@media screen and (max-width: 1350px) {
  .container {
    margin: 0 auto;
  }
}
@media screen and (max-width: 550px) {
  .container {
    max-width: calc(100% - 30px);
  }
}
#jGrowl {
  z-index: 999999;
}

*:focus {
  outline: none;
}

.hide {
  display: none;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -webkit-locale: auto;
  white-space: normal;
}
body.compensate-for-scrollbar {
  overflow: hidden;
}
body::-webkit-scrollbar {
  width: 7px;
}
body::-webkit-scrollbar-track {
  background-color: #1A1919;
}
body::-webkit-scrollbar-thumb {
  border-radius: 8px;
  border: 1px solid #1A1919;
  background-color: #9b8a60;
}

#page {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
}

html {
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
}

.logo {
  display: inline-block;
  flex-shrink: 0;
}
.logo img {
  width: 109px;
  height: 24px;
}

.inner {
  display: flex;
}
.inner-left {
  flex-basis: 23.48285%;
  display: flex;
}
.inner-right {
  flex-basis: 76.51715%;
  display: flex;
}

.title {
  font-family: "Calibri", sans-serif;
  font-weight: bold;
  color: #ffffff;
  position: relative;
  z-index: 1;
}
.title--big {
  font-size: 80px;
  line-height: 77px;
}
@media screen and (max-width: 1200px) {
  .title--big {
    font-size: 64px;
    line-height: 64px;
  }
}
@media screen and (max-width: 993px) {
  .title--big {
    font-size: 52px;
    line-height: 52px;
  }
}
@media screen and (max-width: 767px) {
  .title--big {
    font-size: 45px;
    line-height: 45px;
  }
}
@media screen and (max-width: 400px) {
  .title--big {
    font-size: 40px;
    line-height: 100%;
  }
}
@media screen and (max-width: 370px) {
  .title--big {
    font-size: 35px;
    line-height: 100%;
  }
}
.title--small {
  font-size: 27px;
  line-height: 77px;
}

.section-title {
  font-weight: bold;
  font-size: 27px;
  line-height: 43px;
}

.button {
  font-weight: 500;
  font-size: 16px;
  line-height: 39px;
  padding: 10px 22px;
  background-color: #9b8a60;
  transition: 0.4s;
  color: #ffffff !important;
  text-decoration: none !important;
  font-style: normal !important;
  white-space: nowrap;
  cursor: pointer;
}
.button--big {
  text-transform: uppercase;
  margin-left: 50%;
  transform: translateX(-50%);
}
.button::after {
  display: none;
}
@media screen and (max-width: 767px) {
  .button {
    font-size: 14px;
    line-height: 25px;
  }
}
@media screen and (min-width: 1025px) {
  .button:hover {
    background-color: #847348;
  }
}
.button-prev {
  position: relative;
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #f0f0ed;
  transition: 0.3s;
  font-family: "Calibri", sans-serif;
}
@media screen and (min-width: 1025px) {
  .button-prev:hover {
    color: #9b8a60;
  }
  .button-prev:hover svg {
    fill: #9b8a60;
  }
}
.button-prev-img {
  border: 1px solid rgba(255, 255, 255, 0.13);
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}
.button-prev span {
  margin-left: 8px;
  font-size: 15px;
  line-height: 18px;
  color: inherit;
}
.button-prev:visited {
  color: inherit;
}
.button-prev__icon-arrow-prev {
  fill: #ffffff;
  width: 22px;
  height: 15px;
  transition: 0.3s;
}
.button--white {
  background: #F2F2F2;
  color: #1A1919 !important;
}
@media screen and (min-width: 1025px) {
  .button--white:hover {
    background-color: #1A1919;
    color: #ffffff !important;
  }
}

.start-header {
  padding-top: 32px;
  padding-left: 64px;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
}
@media only screen and (max-width: 993px) {
  .start-header {
    padding-left: 30px;
  }
}
.start-body {
  color: #ffffff;
  display: flex;
  height: 100vh;
  background-color: #1a1919;
  position: relative;
}
@media only screen and (max-width: 1025px) {
  .start-body {
    flex-direction: column;
  }
}
.start-body__title {
  position: relative;
  z-index: 10;
  font-family: "Calibri", sans-serif;
  font-weight: bold;
  font-size: 64px;
  line-height: 55px;
  max-width: 532px;
}
@media only screen and (max-width: 1200px) {
  .start-body__title {
    font-size: 45px;
    line-height: 55px;
  }
}
.start-body__inner {
  position: relative;
  flex-basis: 50%;
  padding-top: 211px;
  padding-bottom: 132px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  padding-left: 64px;
  height: 100%;
}
@media only screen and (max-width: 1559px) {
  .start-body__inner {
    padding-top: 145px;
    padding-bottom: 65px;
  }
}
@media only screen and (max-width: 1025px) {
  .start-body__inner {
    padding-top: 150px;
    padding-bottom: 100px;
    padding-left: 30px;
    flex-basis: auto;
    height: auto;
  }
}
@media only screen and (max-width: 550px) {
  .start-body__inner {
    padding-top: 50px;
    padding-bottom: 50px;
  }
}
@media only screen and (max-width: 550px) {
  .start-body__left {
    padding-top: 100px;
  }
}
@media only screen and (max-width: 350px) {
  .start-body__left {
    padding-top: 80px;
  }
}
@media only screen and (max-width: 1025px) {
  .start-body__right {
    padding-top: 30px;
  }
}
@media only screen and (max-width: 550px) {
  .start-body__right {
    padding-top: 14px;
    padding-bottom: 50px;
  }
}
.start-body__right-bg {
  margin-left: -32px;
  width: calc(100% + 32px) !important;
}
.start-body__button {
  position: relative;
  z-index: 10;
  color: #ffffff;
  font-size: 16px;
  line-height: 39px;
  padding: 10px 22px;
}
@media only screen and (max-width: 1025px) {
  .start-body__button {
    margin-top: 46px;
  }
}
@media only screen and (max-width: 350px) {
  .start-body__button {
    margin-top: 20px;
    padding: 8px 15px;
    font-size: 14px;
  }
}
.start-body__bg {
  display: none;
}
@media only screen and (max-width: 1025px) {
  .start-body__bg {
    display: block;
    width: 100%;
    margin-top: 64px;
  }
}
.start-body__bg img {
  width: 100%;
}
.start-body__image {
  position: absolute;
  left: 0;
  top: 51vh;
  overflow: hidden;
  width: 100%;
}
@media only screen and (max-width: 1559px) {
  .start-body__image {
    top: 50vh;
  }
}
@media only screen and (max-width: 1025px) {
  .start-body__image {
    display: none;
  }
}
.start-body__image svg {
  width: 100%;
}
.start-body__image .i1920 {
  width: calc(100% - 64px);
  margin-left: 64px;
  min-width: calc(100% - 64px);
}
@media only screen and (max-width: 1559px) {
  .start-body__image .i1920 {
    display: none;
  }
}
.start-body__image .i1440 {
  display: none;
  width: calc(100% - 64px);
  margin-left: 64px;
  min-width: calc(100% - 64px);
}
@media only screen and (max-width: 1559px) {
  .start-body__image .i1440 {
    display: block;
  }
}